import Nav from './Nav';
// import MenuButton from './MenuButton';
import Link from 'next/link';
import styled from 'styled-components';

const StyledHeader = styled.header`
  .bar{
    display: flex;
    justify-content: space-between;
    padding: 1rem;
    @media (max-width: 700px) {
        flex-direction: column;
      }
    .burger-container {
        position: fixed;
        top: 3%;
        right: 5%;
        z-index: 999;
        cursor: pointer;
        border: none;
        background: none;
        @media (min-width: 700px) {
          display: none;
      }
      }
      
    .burger-container .burger-line-1, .burger-container .burger-line-2, .burger-container .burger-line-3 {
        border-radius: 2px;
        width: 35px;
        height: 5px;
        background-color: ${props => props.theme.semiblack};
        margin: 6px 0;
        -webkit-transition: 0.4s;
        transition: 0.4s;
      }
      
    .change-burger .burger-line-1 {
        -webkit-transform: rotate(-45deg) translate(-9px, 6px);
        transform: rotate(-45deg) translate(-9px, 6px);
      }
      
    .change-burger .burger-line-2 {
        opacity: 0;
      }
      
    .change-burger .burger-line-3 {
        -webkit-transform: rotate(45deg) translate(-8px, -8px);
        transform: rotate(45deg) translate(-8px, -8px);
      }
  }
`



const Header = () => (
    <StyledHeader>
        <div className = "bar">
            <Link href="/">
              <a><img src="../static/logo.png"/></a>
            </Link>
           <Nav />
           {/* <MenuButton /> */}
        </div>
        <div className = "sub-bar">
           <p>Search</p>
        </div>
        <div>Cart</div>
    </StyledHeader>
);

export default Header;